/* PREFIXED CSS */
.dropdown,
.dropdown div,
.dropdown li,
.dropdown div::after,
.dropdown .carat,
.dropdown .carat:after,
.dropdown .selected::after,
.dropdown:after{
  -webkit-transition: all 150ms ease-in-out;
  -moz-transition: all 150ms ease-in-out;
  -ms-transition: all 150ms ease-in-out;
  transition: all 150ms ease-in-out;
}
.dropdown .selected::after,
.dropdown.scrollable div::after{
  -webkit-pointer-events: none;
  -moz-pointer-events: none;
  -ms-pointer-events: none;
  pointer-events: none;
}
/* WRAPPER */
.dropdown{
  position: relative;
  min-width: 90px;
  cursor: pointer;
  background: none;
  padding: 8px 15px 5px;
  color: #FFF;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}
.dropdown.open{
  z-index: 1002;
}
.dropdown:hover,
.dropdown.focus{
  /*--background:#F7F7F7;--*/
}
/* CARAT */
.dropdown .carat,
.dropdown .carat:after{
  position: absolute;
  right: -1px;
  top: 50%;
  margin-top:-2px;
  z-index: 1;
  background: url(../images/arrow.png);
  width:16px;
  height:10px;
}
.dropdown:hover .carat:after{
  border-top-color: #f4f4f4;
}
.dropdown.focus .carat{
  border-top-color: #f8f8f8;
}
.dropdown.focus .carat:after{
  border-top-color: #0180d1;
}
.dropdown.open .carat{
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}
/* OLD SELECT (HIDDEN) */
.dropdown .old{
  position: absolute;
  left: 0;
  top: 0;
  height: 0;
  width: 0;
  overflow: hidden;
}
.dropdown select{
  position: absolute;
  left: 0px;
  top: 0px;
}
.dropdown.touch select{
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
/* SELECTED FEEDBACK ITEM */ 
.dropdown .selected,
.dropdown li{
  display: block;
  font-size:0.95em;
  overflow: hidden;
  white-space: nowrap;
}
.dropdown .selected::after{
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 60px;
}
/* DROP DOWN WRAPPER */
.dropdown div{
  position: absolute;
  height: 0;
  left: 13px;
  right: 0;
  top: 35px;
  background: #fb4d01;
  overflow: hidden;
  opacity: 0;
  color: #fff;
  width: 90px;
}
.dropdown:hover div{
  background:#4CB1CA;
}
/* Height is adjusted by JS on open */
.dropdown.open div{
  opacity: 1;
  z-index: 2;
}
/* FADE OVERLAY FOR SCROLLING LISTS */
.dropdown.scrollable div::after{
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 50px;
}
.dropdown.scrollable.bottom div::after{
  opacity: 0;
}
/* DROP DOWN LIST */
.dropdown ul{
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  list-style: none;
  overflow: hidden;
  padding:0;
  background:#2bdefc;
}
.dropdown.scrollable.open ul{
  overflow-y: auto;
}
/* DROP DOWN LIST ITEMS */
.dropdown li{
  list-style: none;
  padding:8px;
  border-bottom: 1px solid #05bad8;
}
.dropdown li:last-child {
    border-bottom: 0;
}
/* .focus class is also added on hover */
.dropdown li.focus{
  background:#394962;
  position: relative;
  z-index: 3;
  color: #fff;
}
.dropdown li.active{
  background:#394962;
  color: #fff;
}
