}
.search-form input[type="text"] {
  display: inline-block;
  line-height: normal;
  width: 40%;
}
.one-fifth {
  width: 18.4%;
}
.column {
  float: left;
  margin: 0 0 0 2%;
  min-height: 1px;
  position: relative;
}
.selection-box {
  display: block;
  position: relative;
}
.search-form .column input[type="text"], .search-form .column input[type="submit"], .search-form .column select {
  width: 100%;
}
.selection-box select {
  background: #FFFFFF;
}
.search-form .column .selection-box:after {
  height: 46px;
}
.selection-box:before, .selection-box:after {
  content: "";
  pointer-events: none;
  position: absolute;
}
.three-fifth {
  width: 59.2%;
}
.three-fifth input[type="text"]{
  background: #fff;
  border: 1px solid #ddd;
  color: #CDCDCD;
  padding: 15px;
  display: block;
  font-size: 16px;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  width: 100%;
  outline:none;
}
.one-fifth select {
  background-color: #ffffff;
  background-image: url("../images/select-box.jpg");
  background-position: right center;
  background-repeat: no-repeat;
  border: 1px solid #ddd;
  border-radius: 0;
  color: #666666;
  cursor: pointer;
  display: block;
  font-size: 14px;
  height: 51px;
  outline: medium none;
  padding: 15px;
  width: 100%;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}
.one-fifth input[type="submit"]{
  padding:14px 40px;
}
